<template>
    <button class="ui-button" v-on:click=clicked>
        {{label}}
    </button>
</template>
<script>
export default {
    name:"UiButton",
    props: {
        label: {type: String, required: true, default: "Button"}
    },
    methods:{
        clicked(){
            this.$emit("click",{btn: this.label});
        }
    }
}
</script>
<style scoped>
    .ui-button{
        padding:6px 15px;
        background:#AAA;
        text-align: center;
        color:#000;
        border-radius:3px;
        cursor:pointer;
        font-size:12px;
        border:none;
    }
    .ui-button:focus, .ui-button:active{
        outline: none;
    }
    .ui-button:hover{
        background:#999;
        color:#000;
    }
</style>

