<template>
    <div v-if="!errors.length">
        <text-component v-if="component" :label="component.label" :value="component.action_text"/>
    </div>    
</template>

<script>
// import all child element components
import TextComponent from './TextComponent'

import { mapState } from 'vuex'

export default {
    name: "MasterComponent",
    props:{
        component_id: { type: String, required: true }
    },
    components:{
        TextComponent
    },
    computed: mapState({
        component(state){
            return state.template.tpl_components.find(cmpnt => cmpnt.component_id === this.component_id)
        },
        errors(state){
            return state.template.errors
        }
    }),
    created(){
        this.$store.dispatch('template/getComponentConfiguration',this.component_id);
    }
}
</script>

